/* ===== TIMELINE SCROLL SECTION (UPDATED CENTER VERSION) ===== */

.timeline-container {
  position: relative;
  width: 100%;
  margin-top: var(--space-l); /* <-- сдвигаем линию ВНИЗ, круг тоже уходит ниже */
  padding-bottom: var(--space-2xl);
}

/* Линия ВНУТРИ секции + плавный градиент */
.timeline-center-line {
  position: absolute;
  left: 50%;
  top: 0;
  width: 4px;
  height: 100%;
  transform: translateX(-50%);
  z-index: 2;

  background: linear-gradient(
    to bottom,
    transparent 0%,
    var(--vinyl-red) 20%,
    var(--vinyl-red) 80%,
    transparent 100%
  );
  opacity: 0.9;
}

/* КРУГ — теперь строго по центру экрана */
.timeline-year-fixed {
  position: absolute;          /* был sticky — НЕТ, sticky активируем через класс */                /* <- начальная позиция круга СВЕРХУ линии */
  left: 50%;

  width: 140px;
  height: 140px;

  border-radius: 50%;
  background: var(--vinyl-card);
  border: 4px solid var(--vinyl-red);
  color: white;

  font-size: var(--text-4xl);
  font-weight: 700;

  display: flex;
  align-items: center;
  justify-content: center;

  z-index: 3;
  box-shadow: 0 0 30px rgba(255, 0, 0, 0.25);

  transition: top .25s ease, transform .25s ease;
}

/* Когда круг становится sticky */
.timeline-year-fixed.sticky {
  position: sticky;
  top: 50%;
  transform: translate(-50%, -10%);
}

.timeline-scroll-content {
  display: flex;
  flex-direction: column;
  gap: var(--space-3xl);
  position: relative;
  margin-top: -50px;

}

/* Чтобы текст НЕ попадал под круг по центру */
.timeline-row {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: var(--space-2xl);
  align-items: center;
  padding-block: var(--space-2xl);
}

.timeline-text {
  padding-right: 70px;   /* Отступ под большой круг */
}

.history-scroll-section {
  padding-bottom: var(--space-3xl);
  position: relative;
}

.timeline-text h3 {
  margin-bottom: var(--space-md);
  font-size: var(--text-2xl);
}

.timeline-text p {
  color: var(--vinyl-muted);
  line-height: 1.8;
  white-space: pre-line; /* чтобы большой текст красиво переносился */
  font-size: var(--text-base);
}

.timeline-image img {
  width: 100%;
  aspect-ratio: 1 / 1;     /* квадрат */
  object-fit: cover;
  border-radius: var(--radius-lg);
  box-shadow: var(--shadow-lg);
  border: 1px solid var(--vinyl-border);
}

/* ===== MOBILE ===== */
@media (max-width: 768px) {
  .timeline-row {
    grid-template-columns: 1fr;
  }

  .timeline-center-line {
    display: none;
  }

  .timeline-year-fixed {
    left: 50%;
    width: 80px;
    height: 80px;
    font-size: var(--text-xl);
  }

  .timeline-text {
    padding-right: 0;
    padding-left: 0;
  }
}

/* ===== VINYL HERO SECTION ===== */
.vinyl-hero {
  background: linear-gradient(135deg, #0a0a0a 0%, #1a1a1a 50%, #0a0a0a 100%);
  color: white;
  padding: 100px 0 80px;
  margin-bottom: var(--space-2xl);
  text-align: center;
  position: relative;
  overflow: hidden;
  min-height: 600px;
  display: flex;
  align-items: center;
  justify-content: center;
}

/* Виниловая пластинка */
.vinyl-record {
  position: absolute;
  top: -180px;
  left: 50%;
  transform: translateX(-50%);
  width: 400px;
  height: 400px;
  border-radius: 50%;
  background: 
    radial-gradient(circle at 50% 50%, #222 0%, #111 70%, #000 100%),
    repeating-radial-gradient(circle at 50% 50%, 
      transparent 0px, 
      transparent 1px, 
      rgba(255,255,255,0.1) 2px, 
      rgba(255,255,255,0.1) 3px
    );
  box-shadow: 
    0 0 60px rgba(179, 0, 0, 0.3),
    inset 0 0 60px rgba(0, 0, 0, 0.8),
    0 10px 40px rgba(0, 0, 0, 0.6);
  z-index: 1;
  animation: rotate 20s linear infinite;
  border: 8px solid #111;
}

@keyframes rotate {
  from { transform: translateX(-50%) rotate(0deg); }
  to { transform: translateX(-50%) rotate(360deg); }
}

.record-inner {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 120px;
  height: 120px;
  background: linear-gradient(45deg, var(--vinyl-red), #8b0000);
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  box-shadow: 
    0 0 30px rgba(179, 0, 0, 0.5),
    inset 0 0 20px rgba(0, 0, 0, 0.5);
}

.record-label {
  width: 100px;
  height: 100px;
  background: white;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  box-shadow: inset 0 0 10px rgba(0,0,0,0.2);
}

.label-text {
  font-size: 12px;
  font-weight: 800;
  color: var(--vinyl-red);
  text-transform: uppercase;
  letter-spacing: 1px;
  text-align: center;
  line-height: 1.2;
}

.record-hole {
  position: absolute;
  width: 16px;
  height: 16px;
  background: black;
  border-radius: 50%;
}

.record-glow {
  position: absolute;
  top: -20px;
  left: -20px;
  right: -20px;
  bottom: -20px;
  background: radial-gradient(circle at 50% 50%, rgba(179, 0, 0, 0.1) 0%, transparent 70%);
  border-radius: 50%;
  z-index: -1;
  animation: pulse 3s ease-in-out infinite;
}

@keyframes pulse {
  0%, 100% { opacity: 0.3; }
  50% { opacity: 0.6; }
}

/* Текст Vinyl и Store по бокам */
.vinyl-text-left,
.vinyl-text-right {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  z-index: 2;
  font-size: clamp(3rem, 8vw, 6rem);
  font-weight: 800;
  text-transform: uppercase;
  letter-spacing: 8px;
  color: rgba(255, 255, 255, 0.1);
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.5);
  transition: all 0.5s ease;
  pointer-events: none;
}

.vinyl-text-left {
  left: 5%;
  animation: floatLeft 4s ease-in-out infinite;
}

.vinyl-text-right {
  right: 5%;
  animation: floatRight 4s ease-in-out infinite;
}

@keyframes floatLeft {
  0%, 100% { 
    transform: translateY(-50%) translateX(0);
    opacity: 0.1;
  }
  50% { 
    transform: translateY(-50%) translateX(-10px);
    opacity: 0.15;
  }
}

@keyframes floatRight {
  0%, 100% { 
    transform: translateY(-50%) translateX(0);
    opacity: 0.1;
  }
  50% { 
    transform: translateY(-50%) translateX(10px);
    opacity: 0.15;
  }
}

.vinyl-hero:hover .vinyl-text-left {
  color: rgba(255, 255, 255, 0.28);
  transform: translateY(-50%) translateX(-20px);
}

.vinyl-hero:hover .vinyl-text-right {
  color: rgba(255, 255, 255, 0.28);
