/* ===========================================================
   HERO — breathing gradient + rotating vinyl (VinylStore)
   =========================================================== */

.hero-breath {
  position: relative;
  overflow: hidden;
  padding-block: var(--space-4xl);
  background: var(--vinyl-dark);
  border-bottom: 1px solid var(--vinyl-border);
}

/* ——————————————————————————————
   BREATHING BACKDROP
   —————————————————————————————— */

.hero-breath::before {
  content: "";
  position: absolute;
  inset: 0;
  z-index: 0;

  background:
    radial-gradient(800px 400px at 25% 40%, rgba(139,0,0,0.12), transparent 35%),
    radial-gradient(800px 400px at 80% 65%, rgba(156,23,23,0.08), transparent 35%),
    linear-gradient(180deg, rgba(0,0,0,0.55), rgba(0,0,0,0.7));
}

@keyframes breathGlow {
  0%   { opacity: 0.7; transform: scale(1); filter: blur(26px); }
  50%  { opacity: 1;   transform: scale(1.05); filter: blur(36px); }
  100% { opacity: 0.7; transform: scale(1); filter: blur(26px); }
}

/* ——————————————————————————————
   HERO LAYOUT
   —————————————————————————————— */

.hero-inner {
  position: relative;
  z-index: 2;

  display: grid;
  grid-template-columns: minmax(320px, 1fr) 380px;
  gap: var(--space-3xl);

  align-items: center;

  max-width: 1280px;
  margin: 0 auto;
  padding-inline: var(--space-lg);
}

/* ——————————————————————————————
   TEXT BLOCK (LEFT)
   —————————————————————————————— */

.hero-text {
  max-width: 580px;

  display: flex;
  flex-direction: column;
  gap: var(--space-md);
}

.hero-breath .hero-title {
  font-size: clamp(2.5rem, 4vw, 3.8rem);
  font-weight: 800;
  color: var(--vinyl-text);
  line-height: 1.05;
  position: relative;
}

/* glowing backlight behind title */
.hero-breath .hero-title::after {
  content: "";
  position: absolute;
  left: -20px;
  top: -30%;
  width: 460px;
  height: 180px;

  background: radial-gradient(
    closest-side,
    rgba(255, 20, 20, 0.12),
    rgba(255, 20, 20, 0.04) 40%,
    transparent 70%
  );

  filter: blur(40px);
  z-index: -1;

  animation: breathGlow 6s ease-in-out infinite;
}

.hero-subtitle {
  color: var(--vinyl-muted);
  font-size: clamp(1.1rem, 1.5vw, 1.25rem);
  line-height: 1.65;
  max-width: 55ch;
  opacity: 0.95;
}

/* ——————————————————————————————
   HERO BUTTONS (ISOLATED)
   —————————————————————————————— */

/* Контейнер для кнопок */
.hero-actions {
  display: flex;
  gap: 1rem; /* Отступ между кнопками ~16px */
  flex-wrap: wrap;
  margin-top: 1.5rem;
  justify-content: center; /* ← ДОБАВИЛ: выравнивание по левому краю */
  width: 100%; /* ← ДОБАВИЛ: занимает всю ширину родителя */
}

.hero-btn {
  display: inline-flex;
  align-items: center;
  justify-content: center;

  padding: 0.75rem 1.6rem;
  margin: 0; /* Убедимся что нет лишних margin */

  font-size: 1rem;
  font-weight: 600;
  letter-spacing: 0.4px;

  border-radius: var(--radius-md);
  text-decoration: none;
  cursor: pointer;
  user-select: none;

  transition: all 0.25s ease;
  min-height: 44px; /* Для accessibility */
}

/* primary */
.hero-btn-primary {
  background: var(--vinyl-red);
  color: white;
  border: 1px solid var(--vinyl-red);
  box-shadow: 0 0 14px rgba(255,0,0,0.25);
}

.hero-btn-primary:hover {
  background: #ff3030;
  border-color: #ff3030;
  box-shadow: 0 0 20px rgba(255,0,0,0.35);
  transform: translateY(-2px);
}

/* outline */
.hero-btn-outline {
  background: transparent;
  border: 1px solid var(--vinyl-red);
  color: var(--vinyl-red);
}

.hero-btn-outline:hover {
  background: rgba(255,0,0,0.07);
  color: #ff3a3a;
  border-color: #ff3a3a;
  transform: translateY(-2px);
}

/* Адаптивность для мобилок */
@media (max-width: 768px) {
  .hero-actions {
    flex-direction: column;
    gap: 0.75rem; /* Чуть меньше отступ на мобилках */
  }
  
  .hero-btn {
    width: 100%;
    text-align: center;
  }
}

/* Для очень маленьких экранов */
@media (max-width: 480px) {
  .hero-actions {
    gap: 0.5rem;
  }
  
  .hero-btn {
    padding: 0.6rem 1.2rem;
    font-size: 0.95rem;
  }
}

/* ABOUT HERO — ИЗОЛИРОВАННЫЕ СТИЛИ (НЕ ТРОГАЮТ ГЛАВНУЮ) */

.about-hero {
  position: relative;
  overflow: hidden;
  padding: 160px 0 130px;
  background: radial-gradient(circle at center, #141414, #0d0d0d 70%);
  z-index: 1; /* Устанавливаем базовый z-index для всей секции */
}

/* Свет ДО винила - теперь за пластинкой */
.about-record-glow {
  position: absolute;
  top: 50%;
  left: 50%;
  width: 460px;
  height: 460px;
  transform: translate(-50%, -50%);
  pointer-events: none;
  border-radius: 50%;
  background: radial-gradient(
    circle,
    rgba(255, 60, 60, 0.55),
    rgba(255, 40, 40, 0.32) 50%,
    rgba(255, 30, 30, 0.14) 70%,
    transparent 85%
  );
  filter: blur(55px);
  z-index: 1; /* Теперь за пластинкой, но выше фона */
  animation: aboutRecordGlow 7s ease-in-out infinite;
}

@keyframes aboutRecordGlow {
  0%   { opacity: 0.85; transform: translate(-50%, -50%) scale(1); }
  50%  { opacity: 1;    transform: translate(-50%, -50%) scale(1.08); }
  100% { opacity: 0.85; transform: translate(-50%, -50%) scale(1); }
}

/* Винил - теперь выше дымки */
.about-hero-record {
  position: absolute;
  top: 50%;
  left: 50%;
  width: 420px;
  height: 420px;
  transform: translate(-50%, -50%);
  animation: aboutVinylRotate 18s linear infinite;
  opacity: 0.22;
  z-index: 2; /* Выше чем дымка */
  pointer-events: none;
}

@keyframes aboutVinylRotate {
  from { transform: translate(-50%, -50%) rotate(0); }
  to   { transform: translate(-50%, -50%) rotate(360deg); }
}

.about-record-inner {
  position: relative; /* Добавляем для позиционирования дырки */
  width: 100%;
  height: 100%;
  border-radius: 50%;
  background: repeating-radial-gradient(circle, rgba(255,255,255,0.05) 0 2px, rgba(0,0,0,0.1) 2px 4px);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 2;
}

.about-record-label {
  position: relative; /* Для центрирования дырки */
  width: 120px;
  height: 120px;
  border-radius: 50%;
  background: radial-gradient(circle, #222, #111);
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  z-index: 2;
  box-shadow: inset 0 2px 10px rgba(0,0,0,0.6);
}

.about-record-text {
  color: #fff;
  font-size: 0.75rem;
  font-weight: 700;
  letter-spacing: 1.4px;
  margin-bottom: 8px; /* Отступ для дырки */
}

.about-record-hole {
  position: absolute;
  top: 50%;
  left: 50%;
  width: 10px;
  height: 10px;
  background: #000;
  border-radius: 50%;
  transform: translate(-50%, -50%); /* Центрируем дырку */
  z-index: 3;
}

/* Боковой текст с анимацией */
.about-hero-side-left,
.about-hero-side-right {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  z-index: 3;
  font-size: clamp(2rem, 8vw, 5rem);
  font-weight: 800;
  text-transform: uppercase;
  letter-spacing: 8px;
  color: rgba(255, 255, 255, 0.15);
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.5);
  transition: all 0.5s ease;
  pointer-events: none;
}

.about-hero-side-left { 
  left: 3.5%; 
  animation: floatLeft 4s ease-in-out infinite;
}

.about-hero-side-right { 
  right: 3.5%; 
  animation: floatRight 4s ease-in-out infinite;
}

@keyframes floatLeft {
  0%, 100% { 
    transform: translateY(-50%) translateX(0);
    opacity: 0.1;
  }
  50% { 
    transform: translateY(-50%) translateX(-10px);
    opacity: 0.2;
  }
}

@keyframes floatRight {
  0%, 100% { 
    transform: translateY(-50%) translateX(0);
    opacity: 0.1;
  }
  50% { 
    transform: translateY(-50%) translateX(10px);
    opacity: 0.2;
  }
}

/* Анимация при наведении на всю hero секцию */
.about-hero:hover .about-hero-side-left {
  color: rgba(255, 255, 255, 0.28);
  transform: translateY(-50%) translateX(-20px);
}

.about-hero:hover .about-hero-side-right {
  color: rgba(255, 255, 255, 0.28);
  transform: translateY(-50%) translateX(20px);
}

/* Контент текстового блока */
.about-hero-content {
  position: relative;
  max-width: 600px;
  z-index: 4; /* Выше всего */
}

.about-hero-title {
  font-size: 3rem;
  font-weight: 800;
  margin-bottom: 20px;
  color: white;
}

.about-hero-subtitle {
  color: var(--vinyl-muted);
  font-size: 1.15rem;
  line-height: 1.65;
  max-width: 540px;
  margin-bottom: 32px;
}

/* Ноты - улучшенная анимация */
.about-hero-notes {
  position: absolute;
  inset: 0;
  overflow: hidden;
  z-index: 3; /* Выше пластинки и дымки */
}

.about-hero-notes .note {
  position: absolute;
  font-size: 32px;
  color: rgba(255, 255, 255, 0.25);
  opacity: 0;
  animation: aboutNoteFloat 12s linear infinite;
  z-index: 3;
}

.about-hero-notes .note:nth-child(1) { 
  left: 15%; 
  top: 80%;
  animation-delay: 0s; 
}
.about-hero-notes .note:nth-child(2) { 
  left: 35%; 
  top: 85%;
  animation-delay: 2s; 
}
.about-hero-notes .note:nth-child(3) { 
  left: 60%; 
  top: 75%;
  animation-delay: 4s; 
}
.about-hero-notes .note:nth-child(4) { 
  left: 80%; 
  top: 90%;
  animation-delay: 6s; 
}

@keyframes aboutNoteFloat {
  0% { 
    transform: translateY(0) rotate(0deg) scale(0.8); 
    opacity: 0; 
  }
  15% { 
    opacity: 0.4; 
    transform: translateY(-20px) rotate(45deg) scale(1); 
  }
  60% { 
    opacity: 0.25; 
  }
  90% { 
    opacity: 0.1; 
  }
  100% { 
    transform: translateY(-150vh) rotate(720deg) scale(1.2); 
    opacity: 0; 
  }
